body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  background-color: #000000;
  color: #FFFFFF;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main-content {
  display: flex;
  height: 100vh;
}

.left-panel {
  width: 300px;
  padding: 20px;
  background-color: #111111;
  overflow-y: auto;
  box-sizing: border-box;
}

.right-panel {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  overflow: hidden;
  background-color: #000000;
}

h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.control-panel {
  margin-bottom: 20px;
}

.control-panel label {
  display: block;
  margin-bottom: 10px;
}

.control-panel input,
.control-panel select {
  width: 100%;
  padding: 5px;
  background-color: #222222;
  color: #FFFFFF;
  border: 1px solid #444444;
}

.control-panel input[type="range"] {
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  background: #111111;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.control-panel input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  background: #4CAF50;
  cursor: pointer;
  border-radius: 50%;
}

.control-panel input[type="range"]::-moz-range-thumb {
  width: 15px;
  height: 15px;
  background: #4CAF50;
  cursor: pointer;
  border-radius: 50%;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.gallery button {
  padding: 5px 10px;
  background-color: #222222;
  color: #FFFFFF;
  border: 1px solid #444444;
  cursor: pointer;
}

canvas {
  border: none;
  background-color: transparent; /* Add this line */
}

.visualizer-container {
  position: relative;
  width: 100%;
  height: 100%;
}

.progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 4px;
  background-color: #4CAF50;
  transition: width 0.1s ease-out;
}

.iterations-control {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.iterations-control input {
  width: 50px;
  text-align: center;
  margin: 0 5px;
}

.iterations-control button {
  width: 30px;
  height: 30px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #222222;
  color: #FFFFFF;
  border: 1px solid #444444;
  cursor: pointer;
}

.info-button-container {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
}

.info-button {
  background-color: #000000;
  color: #FFFFFF;
  border: none;
  border-left: 1px solid #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.info-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #222222;
  padding: 15px;
  border: 1px solid #444444;
  width: 300px;
  max-height: 400px;
  overflow-y: auto;
}

.info-dropdown h3 {
  margin-top: 0;
}

.info-dropdown ul {
  padding-left: 20px;
}
